import React, { Component } from "react";
import axios from "axios";
import PropTypes from "prop-types";
import "react-modal-video/css/modal-video.min.css";
import ModalVideo from "react-modal-video";
import { ValidationForm, TextInput, SelectGroup } from "react-bootstrap4-form-validation";

class Showcase extends Component {
    constructor(props) {
        super();
        this.state = {
            isOpen: false
        };
        this.openModal = this.openModal.bind(this);
        super(props);
        this.state = {
            name: "",
            email: "",
            phone: "",
            problem: "",
            successMsg: ""
        };
    }

    handleForm = e => {
        let that = this;
        axios
            .post("https://formcarry.com/s/zER7_IhCyp5", this.state, {
                headers: { Accept: "application/json" }
            })
            .then(function(response) {
                document.getElementById("requestForm").reset();
                that.setState({
                    successMsg: "We received your submission"
                });
                document.getElementById("requestForm").reset();
            })
            .catch(function(error) {});
    };

    handleFields = e => this.setState({ [e.target.name]: e.target.value });

    openModal() {
        this.setState({ isOpen: true });
    }

    render() {
        return (
            <React.Fragment>
                <div id="home" className="main-banner bg-gray">
                    <div className="creative-bg-two" />
                    <div className="pattern" />
                    <div className="pattern-2" />

                    <div className="d-table">
                        <div className="d-table-cell">
                            <div className="container">
                                <div className="row">
                                    <div className="col-lg-6 col-md-6">
                                        <div className="main-banner-content">
                                            <h1>{this.props.showcasenName}</h1>
                                            <p>
                                                {this.props.showcaseDescription}
                                            </p>
                                            <a
                                                href={
                                                    this.props
                                                        .showcaseBtnOneLink
                                                }
                                                className="btn btn-primary"
                                            >
                                                {this.props.showcaseBtnOneText}
                                            </a>
                                            <ModalVideo
                                                channel="youtube"
                                                isOpen={this.state.isOpen}
                                                videoId="vr0qNXmkUJ8"
                                                onClose={() =>
                                                    this.setState({
                                                        isOpen: false
                                                    })
                                                }
                                            />
                                            <button
                                                onClick={this.openModal}
                                                className="btn btn-secondary "
                                            >
                                                <i className="icofont-ui-play" />
                                                {this.props.showcaseBtnTwoText}
                                            </button>
                                        </div>
                                    </div>
                                    <div className="col-lg-5 col-md-6 offset-lg-1 offset-md-0">
                                        <div className="hero-form">
                                            <ValidationForm
                                                    id="requestForm"
                                                    onSubmit={(e, formData, inputs) => {
                                                        e.preventDefault();
                                                        this.handleForm(formData);
                                                    }}
                                            >
                                                <div className="form-group">
                                                    <label>{this.props.showcaseFormUName}</label>
                                                    <TextInput
                                                        name="name"
                                                        id="name"
                                                        required
                                                        successMessage=""
                                                        errorMessage="Please enter your name"
                                                        className="form-control"
                                                        autoComplete="off"
                                                        onChange={
                                                            this.handleFields
                                                        }
                                                    />
                                                </div>

                                                <div className="form-group">
                                                    <label>{this.props.showcaseFormUEmail}</label>
                                                    <TextInput
                                                        name="email"
                                                        id="email"
                                                        type="email"
                                                        required
                                                        successMessage=""
                                                        errorMessage="Please enter your email address"
                                                        className="form-control"
                                                        autoComplete="off"
                                                        onChange={
                                                            this.handleFields
                                                        }
                                                    />
                                                    <div className="help-block with-errors" />
                                                </div>

                                                <div className="form-group">
                                                    <label>{this.props.showcaseFormUPhone}</label>
                                                    <TextInput
                                                        name="phone"
                                                        id="phone"
                                                        type="text"
                                                        required
                                                        successMessage=""
                                                        errorMessage="Please enter your phone number"
                                                        className="form-control"
                                                        autoComplete="off"
                                                        onChange={
                                                            this.handleFields
                                                        }
                                                    />
                                                </div>
                                                
                                                <div className="form-group">
                                                    <label>{this.props.showcaseFormUProblem}</label>
                                                    <SelectGroup 
                                                        name="problem" 
                                                        id="problem"
                                                        value={this.state.color} 
                                                        required 
                                                        errorMessage="Please select a option."
                                                        successMessage=""
                                                        onChange={this.handleFields}>
                                                        <option value="">- Tell us what you need -</option>
                                                        <option value="a website">a website</option>
                                                        <option value="a landing page">a landing page</option>
                                                        <option value="an iPhone app">an iPhone app</option>
                                                        <option value="an Android app">an Android app</option>
                                                    </SelectGroup>
                                                </div>

                                                <button type="submit" className="btn btn-primary">{this.props.showcaseFormBtnName}</button>
                                            </ValidationForm>
                                            {this.state.successMsg !== "" ? (
                                                <h3 className="contactMsg">
                                                    {this.state.successMsg}
                                                </h3>
                                            ) : null}
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="bg-top" />
                    <div className="bg-bottom" />
                </div>
            </React.Fragment>
        );
    }
}

//Props Types
Showcase.propTypes = {
    showcasenName: PropTypes.string,
    showcaseDescription: PropTypes.string,
    showcaseBtnOneLink: PropTypes.string,
    showcaseBtnOneText: PropTypes.string,
    showcaseBtnTwoText: PropTypes.string,
    showcaseFormUName: PropTypes.string,
    showcaseFormUEmail: PropTypes.string,
    showcaseFormUPhone: PropTypes.string,
    showcaseFormUProblem: PropTypes.string,
    showcaseFormBtnName: PropTypes.string,
};

//Default Props
Showcase.defaultProps = {
    showcasenName: "Make your business more profitable",
    showcaseDescription:
        "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. consectetur adipiscing elit, sed do eiusmod tempor incididunt ut.",
    showcaseBtnOneLink: "#about",
    showcaseBtnOneText: "Get Started",
    showcaseBtnTwoLink: "//www.youtube.com/watch?v=vr0qNXmkUJ8",
    showcaseBtnTwoText: "Watch Our Video",
    showcaseFormUName: "Your Name",
    showcaseFormUEmail: "Your Email",
    showcaseFormUPhone: "Phone Number",
    showcaseFormUProblem: "Problem",
    showcaseFormBtnName: "Call to Action",
};

export default Showcase;